.game_header .main_header span#balance {
    height: 32px;
    margin: 0 32px;
    padding: 0 16px;
    color: white;
    border-radius: 40px;;
    background: linear-gradient(to top, #ffb000, #d81719);
    font-size: 16px;
    font-weight: 600;
    line-height: 32px;
}

.game_header .main_header .header_action_panel {
    display: flex;
    align-items: center;
}

.game_header .main_header .header_action_panel button {
    color: #060607;
}

.game_header .main_header .header_action_panel img.avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
}

.dark_mode_toggle {
    margin: 10px;
}
  
.game_wrapper {
    z-index: 1;
    width: 100%;
}

.game_footer {
    background: #33271f;
    color: #b78b3dbb;
    font-size: 1em;
    bottom: 0;
    margin-bottom: 0;
    border-width: 1px 0 0;
    border-radius: 0;
    position: fixed;
    right: 0;
    left: 0;
    z-index: 2;
}

.contents_wrapper {
    height: calc(100vh - 64px);
    margin-top: 64px;
    overflow-y: auto;
    background-color: #f2f3f5;
}

