:root {
  --color-cursor: 220, 90, 90;
  --cursor-outline-shade: 0.3;
  --cursor-size: 10px;
  --cursor-outline-size: 12px;
}

body {
  background-color: #2b2b2b;
  cursor: none;
}

body * {
  cursor: none !important;
}

#cursor-dot,
#cursor-dot-outline {
  z-index: 9999;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.15s ease-in-out, transform 0.15s ease-in-out;
}

#cursor-dot {
  width: 10px;
  width: var(--cursor-size);
  height: 10px;
  height: var(--cursor-size);
  background-color: rgba(220, 90, 90, 1);
  background-color: rgba(var(--color-cursor), 1);
}

#cursor-dot-outline {
  width: 12px;
  width: var(--cursor-outline-size);
  height: 12px;
  height: var(--cursor-outline-size);
  background-color: rgba(220, 90, 90, 0.3);
  background-color: rgba(var(--color-cursor), var(--cursor-outline-shade));
}

.row {
