/** CSS **/

div.game_type img {
  /** Offset the Position **/
  position: relative;
  top: 0;
  margin-top: 0;
  margin-bottom: 10px;

  /** 3D Block Effect **/
  box-shadow: 0 10px 0 0 #6B2A4A;

  /** Make it look pretty **/
  display: block;
  background: #a47;
  color: #eee;
  padding: 1em 2em;
  border: 0;
  cursor: pointer;
  font-size: 1.2em;
  opacity: 0.9;
  border-radius: 1em;
}

div.game_type img:active {
  /** Remove 3D Block Effect on Click **/
  box-shadow: none;
  top: 10px;
  margin-bottom: 0;
}

div.game_type img:hover {
  opacity: 1;
}

div.game_type img:active,
div.game_type img:focus {
  /** Remove Chrome's Ugly Yellow Outline **/
  outline: 0;
}

