/* The fast, new way! */
.table.table-black td {
  background-color: #fff;
  color: #495057;
  position: relative; /* For positioning the pseudo-element */
  box-shadow: 0px 0px 2px 0px rgb(200 200 200 / 0.4);
}

.table.table-black td::before {
  /* Position the pseudo-element. */
  content: ' ';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  /* Create the box shadow at expanded size. */
  box-shadow: 0px 0px 8px 0px rgb(200 200 200 / 0.6);

  /* Hidden by default. */
  opacity: 0;
  transition: opacity 500ms;
}
.table.table-striped td:hover{

  /* Create the box shadow at expanded size. */
  box-shadow: 0px 0px 8px 0px rgb(200 200 200 / 0.4);

  /* Hidden by default. */
  transition: opacity 500ms;
}
.table.table-black td::before {
  /* Position the pseudo-element. */
  content: ' ';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  /* Create the box shadow at expanded size. */
  box-shadow: 0px 0px 8px 0px rgb(200 200 200 / 0.8);

  /* Hidden by default. */
  opacity: 0;
  transition: opacity 500ms;
}

.table td:hover::before{
  /* Show the pseudo-element on hover. */
  opacity: 1;
}
.table tr:hover td{
  transform: scaleY(1.15);
}
/*===================*/
/* BOB
/*===================*/
@keyframes bob-on-hover {
  0% {
    transform: translateY(-2px);
  }
  50% {
    transform: translateY(-.6px);
  }
  100% {
    transform: translateY(-2px);
  }
}
@keyframes bob-on-hover-float {
  100% {
    transform: translateY(-2px);
  }
}

.table tr:hover td, .table tr:focus td, .table tr.bob-on-hover:active td {
  animation-name: bob-on-hover-float, bob-on-hover;
  animation-duration: .3s, 1.5s;
  animation-delay: 0s, .3s;
  animation-timing-function: ease-out, ease-in-out;
  animation-iteration-count: 1, infinite;
  animation-fill-mode: forwards;
  animation-direction: normal, alternate;
}

