        display: none;
    }
    .game_header .main_header .header_action_panel button span.username
    /* ,.game_header .main_header .header_action_panel button svg  */
    {
        display: none;
    }
    .contents_wrapper {
        height: auto;
        height: initial;
    }
}
.contents_wrapper .main-game {
    display: flex;
    position: relative;
}
.contents_wrapper .main-game .main-panel {
    padding: 32px;
}
.contents_wrapper .main-game .sub-panel {
    padding: 32px 24px 8px;
    background-color: rgba(235, 237, 239, 0.5);
    position: static;
    position: initial;
}
.main-game h2.main-title {
    font-size: 18px;
    font-weight: 600;
    color: #060607;
    margin-bottom: 24px;
    text-transform: uppercase;
}
.main-game .main-game-page-tabs {
    border-bottom: 1px solid #b9b9b9;
}
.main-game .main-game-page-tabs .Mui-selected {
    background-color: white;
}
.main-game .main-game-page-tabs.sub-tabs .Mui-selected {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.table_title_with_search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0;
}
.search_panel {
    background: #fff;
    padding-left: 10px;
    border: 3px solid #fff;
    border-radius: 8px;
}
.search_panel input.search_text{
    border: 0;
    outline: 0;
}
.search_panel button {
    color: #888;
    padding: .3rem
}
div.table .table-row,
div.table .table-header {
    background: white;
    padding: 16px;
    margin-bottom: 8px;
    border-radius: 16px; 
    color: #747f8d;
    font-size: 16px;
}
div.table .table-row > div,
div.table .table-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
div.table .table-header {
    background-color: transparent;
    margin: 0;
    text-align: center;
    font-size: 14px;
    padding-bottom: 8px;
}
div.table .table-row .table-cell img.game-type-icon {
    width: 40px;
    height: 40px;
    border-radius: 8px;
    background-color: #f2f3f5;
    margin-right: 16px;
}
div.table .table-row .table-cell img.avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
}
div.table .table-row .table-cell button {
    padding: 0;
    border-radius: 40px;
    color: white;
    line-height: 32px;
    font-weight: 600;;
    background-image: linear-gradient(to bottom, #ff409a, #c438ef);
}
div.table .table-cell button.btn_join {
    width: 114px;
    height: 32px;
}
div.table .table-cell button.btn_end {
    width: 64px;
    height: 32px;
}
div.table.main-game-table .table-row .table-cell.cell-room-info{
    width: 15%;
    display: flex;
    align-items: center;
}
.table-cell.cell-room-info .cell-game-type {
    color: #060607;
    line-height: 19px;
}
.table-cell.cell-room-info .cell-game-id {
    font-size: 14px;
    line-height: 17px;
}
div.table.main-game-table .table-row .table-cell.cell-user-name {
    width: 35%;
}
div.table.main-game-table .table-row .table-cell.cell-user-name i.online-status {
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-left: 8px;
    background-color: #b9bbbe;
    border-radius: 50%;
}
div.table.main-game-table .table-row .table-cell.cell-user-name i.online-status.online {
    background-color: #43b581;
}
div.table.main-game-table .table-row .table-cell.cell-amount-info{
    width: 20%;
    color: #ffb000;
    font-weight: bold;
}
.contents_wrapper .main-game h2 span {
    font-size: 12px;
    font-weight: normal;
    color: #ffb000;
    text-transform: none;
}
.create-game-panel {
    display: flex;
    justify-content: left;
    align-items: center;
    border-bottom: 1px solid rgba(163, 163, 165, 0.3);
    padding-bottom: 20px;
    margin-bottom: 24px;
}
.create-game-panel .btn-create-game {
    margin-right: 20px;
    text-align: center;
}
.create-game-panel .btn-create-game .game-type-icon {
    display: inline-block;
    width: 64px;
    height: 64px;
    background-color: white;
    border-radius: 8px;
    background-position: center;
    background-repeat: no-repeat;
}
