div.table.my-open-game-table {
    text-align: center;
}
div.table.my-open-game-table .table-cell.room-id,
div.table.my-open-game-table .table-cell.winnings,
div.table.my-open-game-table .table-cell.action {
    width: 20%;
}
div.table.my-open-game-table .table-cell.bet-info {
    width: 40%;
}
div.table.my-open-game-table .table-row .table-cell.winnings span {
    font-weight: bold;
    color: #43b581;
}
div.table.my-open-game-table .table-row .table-cell.room-id {
    text-align: left;
    color: #060607;
}
div.table.my-open-game-table .table-row .table-cell.bet-info span.bet-pr {
    color: #ffb000;
    font-weight: bold;

}
div.table.my-open-game-table .table-row .table-cell.bet-info span.end-amount {
    color: #d81719;
    margin-left: 10px;
    font-weight: bold;
}
div.table.my-history-table {
    margin-top: 20px;
}
div.table.my-history-table .table-row .table-cell img.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.main_table_pagination button.btn_main_table_page_number {
    margin: 0 16px 0 0;
    padding: 0 8px;
    font-size: 12px;
    color: #060607;
    line-height: 24px;
    height: 24px;
    background-color: white;
}
.main_table_pagination button.btn_main_table_page_number.active {
    border: 1px solid #ff409a;
}
#game_logs {
    position: absolute;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 1s ease-in-out, transform 0.15s ease-in-out;
}
#game_logs.hidden {
    display: none;
    opacity: 0;
}
#game_logs .arrow-up{
    width: 0;
    height: 0;
    right: 230px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid white;
}
#game_logs .game_logs_contents {
    background: white;
    width: 330px;
    margin: 10px auto;
    border-radius: 10px;
    right: 150px;
    position: absolute;
    padding: 14px 10px;
}
#game_logs .game_logs_contents h2 {
    color: #060607;
    font-size: 16px;
    font-weight: 600;
    text-align: left;
}
#game_logs .game_logs_contents table {
    width: 100%;
    line-height: 33px;
}
#game_logs .game_logs_contents table tr:hover {
    background-color: #f2f3f5;
}
#game_logs .game_logs_contents table td.amount {
    font-size: 14px;
    font-weight: bold;
}
#game_logs .game_logs_contents table td.amount.green {
    color: #43b581;
}
#game_logs .game_logs_contents table td.amount.red {
    color: #d81719;
}
#game_logs .game_logs_contents table td.fromNow {
    text-align: right;
    font-size: 12px;
    font-weight: normal;
    color: #747f8d;
}
@media (min-width: 768px) {
    .mobile-only {
        display: none !important;
    }
}
@media (max-width: 767px) {
    .contents_wrapper .main-game {
        flex-direction: column;
        margin-bottom: 60px;
    }
    .create-game-panel-parent {
        overflow: hidden;
        height: 95px;
    }
    .create-game-panel {
        border-bottom: none;
        width: 100%;
        overflow-x: auto;
    }
    .create-game-panel .btn-create-game {
        margin-right: 16px;
    }
    .create-game-panel .btn-create-game .game-type-icon {
        width: 56px;
        height: 56px;
    }
    .create-game-panel .btn-create-game .game-type-name {
        font-size: 9px;
    }
    .main-game h2.main-title {
        font-size: 16px;
    }
    .contents_wrapper .main-game .main-panel,
    .contents_wrapper .main-game .sub-panel {
        padding: 24px 16px;
    }

    div.table div.table-row div.table-cell {
        width: auto !important;
    }
    .desktop-only {
        display: none;
    }
    .mobile-only {
        display: inline;
        display: initial;
    }
    div.table div.table-row div.table-cell {
        font-size: 14px;
    }
    div.table div.table-row div.mobile-only {
        margin-top: 16px;
    }
    div.table .table-row .table-cell img.avatar {
        width: 16px; 
        height: 16px;
    }
    div.main-page-nav-button-group {
        position: fixed;
        width: 100%;
        bottom: 0;
        box-shadow: 0 -4px 4px 0 rgba(0, 0, 0, 0.14);
    }
    div.main-page-nav-button-group button {
        width: 50%;
        height: 60px;
        background-color: #f2f3f5;
        color: #747f8d;
        font-size: 16px;
        font-weight: 500;
        line-height: 60px;
        border-top: 3px solid #f2f3f5;
        border-radius: 0;
        text-transform: none;
    }
    div.main-page-nav-button-group button:hover,
    div.main-page-nav-button-group button.active {
        color: #060607;
        background-color: white;
        border-top-color: #ff409a;
    }
    div.table.main-game-table .table-row .table-cell.cell-user-name i.online-status {
        width: 8px;
        height: 8px;
    }
    #game_logs .arrow-up{
        right: 160px;
    }
    #game_logs .game_logs_contents {
        right: 50%;
        transform: translate(50%, 0);
    }
}
.Dropzone {
  height: 235px;
  width: 350px;
  border-width: 2px;
  border-style: dashed;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
}

.PreviewPanel {
  height: 233px;
  width: 348px;
  position: absolute;
}

.Highlight {
  background-color: rgb(188, 185, 236);
}

.Icon {
  opacity: 0.3;
  height: 64px;
  width: 64px;
}

.FileInput {
  display: none;
}

.Upload {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  overflow: hidden;
}

.Content {
  display: flex;
  flex-direction: row;
  padding-top: 16px;
  box-sizing: border-box;
  width: 100%;
}

.Files {
  margin-left: 32px;
  align-items: flex-start;
  justify-items: flex-start;
  flex: 1 1;
  overflow-y: auto;
}

.Actions {
  display: flex;
  flex: 1 1;
  width: 100%;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 32px;
}

.Filename {
  margin-bottom: 8px;
  font-size: 16px;
  color: #555;
}

.Row {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: space-between;
  height: 50px;
  padding: 8px;
  overflow: hidden;
  box-sizing: border-box;
}

.CheckIcon {
  opacity: 0.5;
  margin-left: 32px;
}

.ProgressWrapper {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: center;
}

button {
  font-family: "Roboto medium", sans-serif;
  font-size: 14px;
  display: inline-block;
  height: 36px;
  padding: 6px 16px;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 0;
  border-radius: 2px;
  background: rgba(103, 58, 183, 1);
  color: #fff;
  outline: 0;
}

button:disabled {
  background: rgb(189, 189, 189);
  cursor: default;
}

